import React, { useState, useEffect, useRef } from 'react';
import './Chatbot.css';

const Chatbot = () => {
    const [messages, setMessages] = useState([
        { sender: 'bot', text: '!     ?' },
    ]);
    const [input, setInput] = useState('');
    const [face, setFace] = useState('happy.png'); //    
    const [loading, setLoading] = useState(false);
    const chatboxRef = useRef(null);

    const recommendedQuestions = [
        '   ??',
        ',     ?',
        '  ?',
    ];

    const handleSend = async () => {
        if (!input.trim()) return;

        const newMessages = [...messages, { sender: 'user', text: input }];
        setMessages(newMessages);
        setInput('');
        setLoading(true);

        try {
            const res = await fetch('/api/chat', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ message: input }),
            });

            const data = await res.json();

            setMessages((prev) => [...prev, { sender: 'bot', text: data.reply }]);
            if (data.face) {
                setFace(data.face); //    
            }
        } catch (err) {
            console.error(' Chat API :', err);
            setMessages((prev) => [...prev, { sender: 'bot', text: '   .' }]);
        } finally {
            setLoading(false);
        }
    };

    //   
    useEffect(() => {
        if (chatboxRef.current) {
            chatboxRef.current.scrollTop = chatboxRef.current.scrollHeight;
        }
    }, [messages]);

    //     vh 
    useEffect(() => {
        const setVh = () => {
            const vh = window.innerHeight * 0.01;
            document.documentElement.style.setProperty('--vh', `${vh}px`);
        };
        setVh();
        window.addEventListener('resize', setVh);
        return () => window.removeEventListener('resize', setVh);
    }, []);

    const handleRecommendClick = (text) => {
        setInput(text);
    };

    return (
        <div className="chatbot-container">
            <div className="chatbot-sidebar">
                <div className="wishbot-profile">
                    {/*     */}
                    <img className="wishbot-image" src={`/face/${face}`} alt=" " />
                    <div className="wishbot-name"></div>
                </div>
                <h3> </h3>
                {recommendedQuestions.map((q, idx) => (
                    <button key={idx} onClick={() => handleRecommendClick(q)} className="question-btn">
                        {q}
                    </button>
                ))}
            </div>

            <div className="chatbot-main">
                <div className="chatbox" ref={chatboxRef}>
                    {messages.map((msg, idx) => (
                        <div key={idx} className={`chat-message ${msg.sender}`}>
                            <div className="bubble">{msg.text}</div>
                        </div>
                    ))}
                    {loading && (
                        <div className="chat-message bot">
                            <div className="bubble loading">
                                 <span className="dot">.</span>
                                <span className="dot">.</span>
                                <span className="dot">.</span>
                            </div>
                        </div>
                    )}
                </div>

                <div className="input-form">
                    <input
                        type="text"
                        placeholder=" ..."
                        value={input}
                        onChange={(e) => setInput(e.target.value)}
                        onKeyDown={(e) => e.key === 'Enter' && handleSend()}
                    />
                    <button onClick={handleSend}></button>
                </div>
            </div>
        </div>
    );
};

export default Chatbot;
